.Aboutme {
  height: auto;
  background-color: #010834;
  display: flex;
  flex-direction: column;
  color: white;
  align-items: center;
  padding-top: 40px;
  gap: 32px;
  padding-bottom: 200px;
}

.Aboutme h1 {
  font-size: 48px;
  font-weight: bold;
}

.Aboutme h2 {
  font-size: 42px;
  color: #bad8ff;
}

.Aboutme img {
  width: 200px;
}

.Aboutme p {
  font-size: 22px;
  padding: 0 20%;
}

.findme-links {
  margin-top: 50px;
}

.findme-link-svg {
  margin-top: 50px;
  display: flex;
  height: 200px;
  justify-content: center;
  gap: 80px;
}

.findme-links svg {
  height: 150px;
  fill: #ffffff;
  transition: all 300ms;
}

.findme-links svg:hover {
  fill: #008cff;
}
